@import url(https://fonts.googleapis.com/css2?family=Karla:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap);
html,body{-ms-scroll-chaining:none;overscroll-behavior:none}*{margin:0;padding:0;color:#fff;font-family:"Karla"}.body{min-height:100vh;width:100%;background:none}header{height:150px;background-image:linear-gradient(to right, #6a11cb 0%, #2575fc 100%);font-size:100px;padding-top:20px;text-align:center}.container{background:linear-gradient(45deg, #f43b47 0%, #453a94 100%);display:flex;flex-wrap:wrap;justify-content:center;min-height:80vh;padding:20px 0px}footer{height:50px;width:100%;background-image:linear-gradient(to right, #6a11cb 0%, #2575fc 100%);position:relative;bottom:0;text-align:center;font-size:30px;padding:25px 0px}button{height:150px;width:400px;border-radius:30px;background-color:#f9ac06;font-size:80px;place-self:center}.card{height:500px;width:400px;border-radius:20px;background:#b6bcdc;margin:20px;display:grid;place-items:center}.card div{height:80%;width:80%;background:none;display:grid;place-items:center;overflow:hidden;border-radius:20px}.card img{height:400px;border-radius:20px}.score{background:linear-gradient(45deg, #09203f 0%, #537895 100%);display:grid;place-items:center;height:100px;font-size:80px;width:100%}@media screen and (max-width: 400px){header{font-size:50px;height:130px}.container{min-height:300px}footer{height:30px;font-size:15px;padding-bottom:10px}.card{height:250px;width:200px}.card div{background:none}.card div img{height:200px}.score{font-size:50px}button{height:100px;width:200px;font-size:30px}}
